import tab from 'base/tab/tab'
import Tarbar from 'base/tarbar/tarbar'
import { getHeader, getLike ,getMenuList } from 'api/api'
import Scroll from 'base/scroll/scroll'
import BScroll from 'better-scroll'
import ShTitle from 'base/title/title'
import { mapMutations, mapGetters } from 'vuex'
export default {
  name: 'Index',
  data () {
    return {
      dots: false,
      data: [],
      classify: [],
      recommend: [],
      topTitle: [],
      premiumLife: [],
      tops: [],
      slider: [],
      farmrecommend: [],
      like: [],
      menuLists: []
    }
  },
  created () {
    this._getLike()
    this._getHeader()
  },
  mounted () {
    this.$nextTick(() => {
      this._initPics()
      this._initPics02()
    })
  },
  watch: {
    'tops' () {
      this.$nextTick(() => {
        this._initPics()
      })
    },
    'like' () {
      // 重新计算高度
      // this.$refs.scroll.refresh()
    },
    'farmrecommend' () {
      this.$nextTick(() => {
        this._initPics02()
      })
    }
  },
  computed: {
    ...mapGetters([
      'city'
    ])
  },
  methods: {
    _initPics () {
      let picWidth = 120
      let margin = 10
      let width = (picWidth + margin) * this.tops.length - margin
      this.$refs.picList.style.width = width + 'px'
      this.$nextTick(() => {
        this.picScroll = new BScroll(this.$refs.picWrapper, {
          scrollX: true,
          eventPassthrough: 'vertical'
        })
      })
    },
    toSearch() {
      this.$router.push({
				path: `/search`
			})
    },
    toDeail (id) {
      let objData = {'id':id}
      this.setGoods(objData)
      this.$router.push({
				path: `/detail?id=${id}`
			})
      this.setGoods(objData)
    },
    selectCity () {
      this.$router.push({
				path: `/city`
			})
    },
    _initPics02 () {
      let picWidth = 300
      let margin = 10
      let width = (picWidth + margin) * this.farmrecommend.length - margin + 35
      this.$refs.picList02.style.width = width + 'px'
      this.$nextTick(() => {
        this.picScroll = new BScroll(this.$refs.picWrapper02, {
          scrollX: true,
          eventPassthrough: 'vertical'
        })
      })
    },
    _getMenuList(item) {
      // console.log(item)
      let sliderid = item.sliderid
      let cat_url = item.id
      let page = 1
      getMenuList(sliderid,cat_url,page).then((res) => {
        this.menuLists = res.data
        // console.log(this.menuLists[0].image)
      })
    },
    _getHeader () {
      getHeader().then((res) => {
          // header 轮播图
        let slider = res.data.filter(function (e) {
          return e.styleID === 'slider_02'
        })
          // header Tab 切换
        let tops = res.data.filter(function (e) {
          return e.styleID === 'topgoods_00'
        })
          // 有品生活
        let premiumLife = res.data.filter(function (e) {
          return e.styleID === 'premiumLife_00'
        })
          // 精选推荐
        let recommend = res.data.filter(function (e) {
          return e.styleID === 'jingxuangoods_00'
        })
          // 异域诱惑/ 水果世界 / 坚果计划 / 绿色食品等
        let classify = res.data.filter(function (e) {
          return e.styleID === 'classify_02'
        })
          // 农场推荐
        let farmrecommend = res.data.filter(function (e) {
          return e.styleID === 'farmrecommend_01'
        })

        this.data = res.data
        this.slider = slider[0].items
        this.topTitle = tops[0]
        this.premiumLife = premiumLife[0]
        this.recommend = recommend[0]
        this.classify = classify
        this.tops = tops[0].items
        this.farmrecommend = farmrecommend[0].items
      })
    },
    _getLike () {
      getLike().then((res) => {
        let like = res.data
        this.like = like
      })
    },
    selectMenu (index,item) {
      if(index > 0 ){
        this.data = []
        this.slider = []
        this.topTitle = []
        this.premiumLife = []
        this.recommend = []
        this.classify = []
        this.tops = []
        this.farmrecommend = []
        this.like= []
        this._getMenuList(item)
      }else {
        this._getLike()
        this._getHeader()
        this.menuLists = []
      }
    },
    ...mapMutations({
      setGoods: 'SET_GOODS'
	  })
  },
  components: {
    tab,
    ShTitle,
    Tarbar,
    Scroll
  }
}



